
body{
    background-color: #222;
    min-height: 1000px;
    color: white;
}
.center{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.center-container {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    min-width: 500px;
    min-height: 600px;
    background-image: url(static/img/mountains.536f7d9.jpeg);
    /* background-image: linear-gradient(to bottom, rgba(245, 246, 252, 0.52), rgba(117, 19, 93, 0.73)), url(' ./assets/mountains.jpeg'); */
    /* background-image: linear-gradient(to bottom, #b331237a, #9b120617), url(' ./assets/mountains.jpeg'); */
    background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(126, 34, 33, 0.5)), to(rgba(58, 9, 4, 0.6))), url(static/img/mountains.536f7d9.jpeg);
    background-image: linear-gradient(to bottom,rgba(126, 34, 33, 0.5), rgba(58, 9, 4, 0.6)), url(static/img/mountains.536f7d9.jpeg);

    /* background-image: linear-gradient(to bottom,#444E8DCC, #222222AA), url(' ./assets/mountains.jpeg'); */
    background-position-x: -100px;
    background-position-y: 0px;
     background-blend-mode: normal;
}
#app {
  font-family: 'Calibri', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;

  height: 100%;
  margin: 0;
}

#resort h1{
  color: white;
  text-transform: uppercase;
  margin: 0;
  margin-top:20px;
}


#new-snow {
  background-color: #222222b0;
  border-bottom: 1px;
}
#new-snow #overnight-header{
  text-align: left;
}
#new-snow #day-header{
  text-align: left;
}
#new-snow #overnight-label{
  text-align: left;
  text-transform: uppercase;
}
#new-snow #day-label{
  text-align: left;
  text-transform: uppercase;
}

#travel-time{
  background-color: #222222b0;
  border-bottom: 1px;
}
#travel-time .left{
  display: inline;
}
#travel-time .right{
  display: inline;
}


